                            ctx.beginPath()
                            ctx.moveTo(cornerPoints[0][0], cornerPoints[0][1] + fontSize / 2)
                            ctx.lineTo(cornerPoints[1][0], cornerPoints[1][1] + fontSize / 2)
                            ctx.stroke()
