                            ctx.strokeStyle = setting.textDecorationUnder.color;
                            ctx.lineWidth = setting.textDecorationUnder.lineWidth;
                            ctx.beginPath()
                            ctx.moveTo(cornerPoints[3][0], cornerPoints[3][1])
